#app2 {
  border: 1px solid green;
  height: 50vh;
  width: 50vw;
}

.tab-bar {
  display: flex;
}

.tab-bar > li {
  background: grey;
  border: 1px solid black;
  width: 50%;
}

.tab-bar > li.selected {
  background: orange;
}

.tab-content {
}

.tab-content > li {
  display: none;
}

.tab-content > li.active {
  display: block;
}
